"use client";

import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import Button from "@mui/material/Button";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemText from "@mui/material/ListItemText";
import MenuIcon from "@mui/icons-material/Menu";
import KeyboardArrowDown from "@mui/icons-material/KeyboardArrowDown";

export default function Navbar() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [drawerOpen, setDrawerOpen] = React.useState(false);

  // Dropdown open/close
  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <AppBar
      position="sticky"
      elevation={1}
      sx={{
        background: "#ffffff",
        color: "#000",
        px: 2,
        py: 1,
      }}
    >
      <Toolbar>

        {/* ------------ LOGO ------------ */}
        <Typography
          variant="h6"
          sx={{ flexGrow: 1, fontWeight: 700, cursor: "pointer" }}
        >
          PHPtravels
        </Typography>

        {/* ------------ DESKTOP MENU ------------ */}
        <Box sx={{ display: { xs: "none", md: "flex" }, gap: 2 }}>
          <Button color="inherit">Home</Button>

          {/* ----- Dropdown: Flights ----- */}
          <Button
            color="inherit"
            endIcon={<KeyboardArrowDown />}
            onClick={handleMenuOpen}
          >
            Flights
          </Button>

          <Menu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleMenuClose}
          >
            <MenuItem onClick={handleMenuClose}>Flight Search</MenuItem>
            <MenuItem onClick={handleMenuClose}>Flight Booking</MenuItem>
          </Menu>

          <Button color="inherit">Hotels</Button>
          <Button color="inherit">Tours</Button>
          <Button color="inherit">Cars</Button>
          <Button variant="contained" sx={{ ml: 2 }}>
            Login
          </Button>
        </Box>

        {/* ------------ MOBILE MENU BUTTON ------------ */}
        <IconButton
          sx={{ display: { xs: "flex", md: "none" } }}
          onClick={() => setDrawerOpen(true)}
        >
          <MenuIcon />
        </IconButton>
      </Toolbar>

      {/* ------------ MOBILE DRAWER ------------ */}
      <Drawer
        anchor="left"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
      >
        <Box sx={{ width: 250 }} role="presentation">
          <List>

            <ListItem disablePadding>
              <ListItemButton>
                <ListItemText primary="Home" />
              </ListItemButton>
            </ListItem>

            <ListItem disablePadding>
              <ListItemButton>
                <ListItemText primary="Flights" />
              </ListItemButton>
            </ListItem>

            <ListItem disablePadding>
              <ListItemButton>
                <ListItemText primary="Hotels" />
              </ListItemButton>
            </ListItem>

            <ListItem disablePadding>
              <ListItemButton>
                <ListItemText primary="Tours" />
              </ListItemButton>
            </ListItem>

            <ListItem disablePadding>
              <ListItemButton>
                <ListItemText primary="Cars" />
              </ListItemButton>
            </ListItem>

          </List>
        </Box>
      </Drawer>
    </AppBar>
  );
}
